/* CSS styles */

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f0f0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="0 0 40 40"><text x="0" y="15" font-size="20" fill="rgba(0,0,0,0.1)">+</text><text x="20" y="15" font-size="20" fill="rgba(0,0,0,0.1)">-</text><text x="0" y="35" font-size="20" fill="rgba(0,0,0,0.1)">ร</text><text x="20" y="35" font-size="20" fill="rgba(0,0,0,0.1)">รท</text></svg>');
  background-size: 40px 40px;
}

.container {
  text-align: center;
  width: 50%;
  min-width: 400px;
  max-width: 800px;
  margin: 0 auto;
  border: 1px solid #ddd;
  padding: 40px;
  border-radius: 12px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 1;
}

.question-container {
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
  text-align: left;
  background-color: #f9f9f9;
}

.question-heading {
  text-align: center;
  margin-bottom: 10px;
  color: #333;
}

.question-text {
  font-size: 18px;
  margin-bottom: 10px;
  color: #555;
}

.options-container {
  text-align: left;
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  margin: 5px;
  border-radius: 8px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:disabled {
  background-color: #c0c0c0;
  cursor: not-allowed;
}

button:hover:not(:disabled) {
  background-color: #0056b3;
}

.button-container {
  display: flex;
  justify-content: space-between;
}

.button-left {
  margin-right: auto;
}

.button-right {
  margin-left: auto;
}

.header-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 800px;
  border-radius: 12px;
  margin-bottom: 20px;
}

.header-title {
  margin: 0;
  font-size: 32px;
}

.restart-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
  background-color: #ff4d4d;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.restart-button:hover {
  background-color: #ff1a1a;
}

.restart-button:disabled {
  background-color: #c0c0c0;
  cursor: not-allowed;
}